// __TESTS__/screens/MyPostsScreen.test.tsx
import React from 'react';
import { render, fireEvent, waitFor } from '@testing-library/react-native';
import MyPostsScreen from '../../src/screens/MyPostsScreen';
import postService from '../../src/services/api/postService';
import { Alert } from 'react-native';

// Paper 컴포넌트 모킹 (기존과 동일)
jest.mock('react-native-paper', () => {
  // 기존 모킹 코드 유지
  const mockPaper = {
    Button: ({ onPress, children }) => <button onClick={onPress}>{children}</button>,
    Card: {
      Content: ({ children }) => <div>{children}</div>,
      Actions: ({ children }) => <div>{children}</div>,
    },
    Chip: ({ children }) => <span>{children}</span>,
    Dialog: {
      Title: ({ children }) => <h2>{children}</h2>,
      Content: ({ children }) => <div>{children}</div>,
      Actions: ({ children }) => <div>{children}</div>,
    },
    Portal: ({ children }) => <div>{children}</div>,
  };

  mockPaper.Card.Content = mockPaper.Card.Content;
  mockPaper.Card.Actions = mockPaper.Card.Actions;
  mockPaper.Dialog.Title = mockPaper.Dialog.Title;
  mockPaper.Dialog.Content = mockPaper.Dialog.Content;
  mockPaper.Dialog.Actions = mockPaper.Dialog.Actions;
  mockPaper.Card = Object.assign(
    ({ children }) => <div>{children}</div>,
    mockPaper.Card
  );
  mockPaper.Dialog = Object.assign(
    ({ children, visible }) => visible ? <div>{children}</div> : null,
    mockPaper.Dialog
  );

  return mockPaper;
});

// 네비게이션 모킹
jest.mock('@react-navigation/native', () => ({
  useNavigation: () => ({
    navigate: jest.fn(),
    goBack: jest.fn(),
    addListener: jest.fn(() => jest.fn()),
  }),
}));

// 벡터 아이콘 모킹
jest.mock('react-native-vector-icons/MaterialCommunityIcons', () => 'Icon');

// 서비스 모킹
jest.mock('../../src/services/api/postService', () => ({
  getMyPosts: jest.fn(() => Promise.resolve({
    data: {
      posts: [
        {
          post_id: 1,
          content: '첫 번째 테스트 게시물입니다.',
          emotion_summary: '행복',
          like_count: 5,
          comment_count: 2,
          created_at: '2025-03-01T12:00:00Z',
        }
      ]
    }
  })),
  deletePost: jest.fn(() => Promise.resolve({ data: { success: true } })),
}));

// Alert 모킹
jest.mock('react-native/Libraries/Alert/Alert', () => ({
  alert: jest.fn(),
}));

describe('MyPostsScreen', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  // 기존 테스트 - 마운트 시 데이터 가져오는지 확인
  it('fetches posts on mount', async () => {
    const mockNavigation = {
      navigate: jest.fn(),
      addListener: jest.fn(() => jest.fn()),
    };
    
    render(<MyPostsScreen navigation={mockNavigation} route={{}} />);
    
    await waitFor(() => {
      expect(postService.getMyPosts).toHaveBeenCalled();
    }, { timeout: 1000 });
  });

  // 수정된 테스트 - 데이터 로딩 상태 확인
  it('calls navigation.navigate with CreatePost when new post button is pressed', async () => {
    const mockNavigation = {
      navigate: jest.fn(),
      addListener: jest.fn(() => jest.fn()),
    };
    
    const { findByTestId } = render(
      <MyPostsScreen navigation={mockNavigation} route={{}} />
    );
    
    // UI 요소에 testID 프로퍼티를 사용하여 요소 찾기 (컴포넌트에 이 ID가 있어야 함)
    const newPostButton = await findByTestId('new-post-button');
    fireEvent.press(newPostButton);
    
    expect(mockNavigation.navigate).toHaveBeenCalledWith('CreatePost');
  });

  // 데이터 로드 중 에러 처리 확인
  it('shows alert when error occurs during data loading', async () => {
    // 이번 테스트에서만 에러 발생하도록 임시 모킹
    postService.getMyPosts.mockImplementationOnce(() => 
      Promise.reject(new Error('네트워크 오류'))
    );
    
    const mockNavigation = {
      navigate: jest.fn(),
      addListener: jest.fn(() => jest.fn()),
    };
    
    render(<MyPostsScreen navigation={mockNavigation} route={{}} />);
    
    await waitFor(() => {
      expect(Alert.alert).toHaveBeenCalledWith('오류', '게시물을 불러오는 중 오류가 발생했습니다');
    }, { timeout: 1000 });
  });
});